@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

body {
  margin: 0 30px;
  font-family: "Poppins";
  background: rgb(202, 199, 199);
}
h1,
h2,
h3,
h4 {
  font-weight: 500;
}
.App {
  font-size: 1.2em;
  margin: 10px auto;
  width: 100%;
  max-width: 1200px;
  padding: 20px;
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: #1606eb;
  border-bottom: 1px dotted;
}
.site-header h1 {
  font-size: 2.5em;
  color: #26b4be;
  padding-bottom: 10px;
  border-bottom: 2px solid;
}

.site-header .categories {
  text-align: right;
}
.site-header .categories a {
  margin-left: 10px;
}

/* review list */
.review-card {
  background: rgb(250, 248, 248);
  margin: 60px auto;
  padding: 1px 20px 20px 90px;
  position: relative;
}
.review-card .rating {
  position: absolute;
  top: -20px;
  left: -10px;
  background: rgb(8, 89, 114);
  font-size: 3em;
  width: 80px;
  height: 80px;
  text-align: center;
  color: white;
}
.review-card h2 {
  margin-bottom: 0;
}
.review-card small {
  margin-right: 10px;
  color: #777;
}
